import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { IMapKeyValue, IOptionsPsActionHyperlink, IOptionsPsContainerLookupOptionComponentExposed } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsTemplateListPage } from 'src/app/commonSRC/psTemplates/ps-template-list/ps-template-list.template';
import { IOptionsPsLocalBeneficiaryExposed } from './ps-local-beneficiary.component.interfaces';


/**
 * @author Aftab.Ali
 * @since 04/02/2020
 *
 * <p> PsLocalBeneficiaryComponent is business component contains mapping for for container-lookup component to load local beneficiaries</p>
 */
@Component({
  selector: 'ps-local-beneficiary',
  templateUrl: './ps-local-beneficiary.component.html',
  styleUrls: ['./ps-local-beneficiary.component.scss'],
})
export class PsLocalBeneficiaryComponent extends PsTemplateListPage implements OnInit {
  @Output() reloadFct = new EventEmitter<string>();
  @Input() options: IOptionsPsLocalBeneficiaryExposed = {};
  // tslint:disable-next-line: no-output-on-prefix
  @Output() public onClickCard = new EventEmitter<any>();
  public labelValuesMap = new Map<string, IMapKeyValue>();
  public requestMap = new Map<string, string>();
  public editRequestMap = new Map<string, string>();
  public headerMap = new Map<string, IMapKeyValue>();
  private showSingleAccount = false;
  public addBeneficiaryOptions: IOptionsPsActionHyperlink = {}
  public updateList: any[];
  public bRefresh = false;

  constructor(
  ) {
    super();
  }

  ngOnInit() {
    super.init();
    if (this.options.hyperLink) {
      this.addBeneficiaryOptions = this.options.hyperLink;
    }

    this.populateLocalBeneficiariesMap();
    if (this.options.showOnlyList === null || this.options.showOnlyList === undefined) {
      this.options.showOnlyList = false;
    }
    // this.containerLookUpOptions.balanceMapping = this.balanceMapping();
    if (this.options.listOfOptions) {
      this.options.listOfOptions.map((element, index) => element['rowIndex'] = index);
      this.options.itemList = this.options.listOfOptions;
    }

  }


  getContainerLookUpOptions() {
    return this.prepareLookUp();
  }

  private prepareLookUp(): any {
    let lookupDetails: IOptionsPsContainerLookupOptionComponentExposed = {};
    lookupDetails.labelKey = this.options.labelKey;
    lookupDetails.fcName = this.options.fcName;
    lookupDetails.currencyFlag = 'currencyDesc';
    lookupDetails.labelsValueMap = this.labelValuesMap;
    lookupDetails.headerMap = this.headerMap;
    lookupDetails.formGroup = this.options.group;
    lookupDetails.itemList = this.options.itemList;
    lookupDetails.showInitialCardValues = 2;
    lookupDetails.editActionUrl = PsCommonBusinessSettings.serviceUrl.personalizationNicknameEndPoint;
    lookupDetails.requestMap = this.requestMap;
    lookupDetails.editRequestMap = this.editRequestMap;
    lookupDetails.group = this.options.group;

    // The detailServiceUrl will load the detail of any service in which
    // paramsKeyLabels is the request parameter
    // responseKeyLabel is the property to be displayed on itemCard key from the response.
    lookupDetails.detailServiceUrl = [
      { url: PsCommonBusinessSettings.serviceUrl.returnCategorySubcategory, paramsKeyLabels: [{ key: 'includeIds', value: 'bank' }, { key: 'type', staticValue: 'Bank' }], responseKeyLabel: [{ key: 'categoriesDescription', value: 'bank' }] }];
    lookupDetails.actionDetailsOptions = [
      {
        id: 'delete',
        buttonIcon: 'delete',
        labelOptions: {
          labelKey: 'delete_key'
        },
        actionUrl: PsCommonBusinessSettings.serviceUrl.deleteBeneficiaryEndPoint,
        cssClass: 'delete-benef fab-edit-button',
        label: 'delete_key',
        iconName: 'trash',
        actionType: 'delete',
        alertType: 'prompt',
        alertStyle: 'info',
        reloadList: true
      },
      {
        id: 'details',
        label: 'details_key',
        actionHyperlink: {
          group: this.options.group,
          iconOptions: {
            iconName: 'document',
            labelOptions: {
              labelKey: 'details_key'
            }
          },
          route: this.options.type === CommonBussinessConstant.LOCAL_BENEFICIARY_TYPE ? 'local-beneficiary' : 'local-card-beneficiary',
          navigationOptions: {
            queryParams: { readOnlypage: true },
          },
          pageOptions: {
            operId: this.options.type === CommonBussinessConstant.LOCAL_BENEFICIARY_TYPE ?
              CommonBussinessConstant.LOCAL_BENEFICIARY_OPER_ID : CommonBussinessConstant.LOCAL_CARD_BENEFICIARY_OPER_ID,
            iconName: 'document',
            title: 'additional_details_key'
          }
        },
        cssClass: 'details-benef local-trasnfer-actions',
      },
      {
        id: 'edit',
        label: 'edit_key',
        actionHyperlink: {
          group: this.options.group,
          iconOptions: {
            iconName: 'edit1',
            labelOptions: {
              labelKey: 'edit_key'
            }
          },
          route: this.options.type === CommonBussinessConstant.LOCAL_BENEFICIARY_TYPE ? 'local-beneficiary' : 'local-card-beneficiary',
          navigationOptions: {
            queryParams: { readOnlypage: false },
          },
          pageOptions: {
            operId: this.options.type === CommonBussinessConstant.LOCAL_BENEFICIARY_TYPE ?
              CommonBussinessConstant.LOCAL_BENEFICIARY_OPER_ID : CommonBussinessConstant.LOCAL_CARD_BENEFICIARY_OPER_ID,
            iconName: 'edit1',
            title: 'edit_key'
          }
        },
        cssClass: 'edit-benef local-trasnfer-actions',
      },
      {
        id: 'pay',
        label: 'pay_key',
        actionHyperlink: {
          group: this.options.group,
          iconOptions: {
            iconName: 'create',
            labelOptions: {
              labelKey: 'pay_key'
            }
          },
          route: this.options.type === CommonBussinessConstant.LOCAL_BENEFICIARY_TYPE ? 'payment' : 'local_credit_card_settlement',
          navigationOptions: {
            queryParams: { readOnlypage: false },
          },
          pageOptions: {
            operId: this.options.type === CommonBussinessConstant.LOCAL_BENEFICIARY_TYPE ? CommonBussinessConstant.LOCAL_BENEFICIARY_TRANSFER : CommonBussinessConstant.LOCAL_CREDIT_CARD_SETTLEMENT,
            iconName: 'create',
            title: this.options.type === CommonBussinessConstant.LOCAL_BENEFICIARY_TYPE ? 'payment_details_key' : 'local_credit_card_settlement_key'
          },
          preCallFunction: {
            func() {
              return new Promise<any>((resolve, reject) => {
                resolve(this.executionClass.prepareTransaction(lookupDetails.actionDetailsOptions[3].actionHyperlink));
              });
            },
            executionClass: this,
            params: []
          },
        },
        cssClass: 'pay-benef local-trasnfer-actions'
      }
    ];
    lookupDetails.statementOptions = {};
    lookupDetails.isEditable = this.options.isEditable;
    lookupDetails.reloadFunction = {
      func() {
        return new Promise<any>((resolve, reject) => {
          resolve(this.executionClass.reloadList());
        });
      },
      executionClass: this,
      params: []
    }
    return lookupDetails;
  }

  reloadList() {
    this.reloadFct.emit();
  }

  public updateCard(account: any) {
    if (account && this.options.listOfOptions) {
      this.options.itemList = this.options.listOfOptions.filter(acct => acct === account);
      this.options.item = this.options.itemList[0];
      this.showSingleAccount = !this.showSingleAccount;
      this.options.showItemPopUp = !this.options.showItemPopUp;
      // this.options.showSelectedCard = false;
    }
    this.onClickCard.emit(this.options);
  }



  /**
   * populating the map for internal beneficiaries
   */
  private populateLocalBeneficiariesMap() {
    let beneficiaryAccountNumberMap: IMapKeyValue = {};
    let beneficiaryCardNumberMap: IMapKeyValue = {};
    let beneficiaryNameMap: IMapKeyValue = {};
    let beneficiaryBankMap: IMapKeyValue = {};
    let benefBankBranchMap: IMapKeyValue = {};
    let beneficiaryBvnMap: IMapKeyValue = {};
    let headerTitleMap: IMapKeyValue = {};
    let headerSubtitleMap: IMapKeyValue = {};

    beneficiaryAccountNumberMap = {
      key: 'localAccountNumberKey',
      value: '--- --- ---',
      isEdit: false,
      showMappingOnNoValue: true
    };

    beneficiaryCardNumberMap = {
      key: 'localCardNumberKey',
      value: '--- --- ---',
      isEdit: false,
      showMappingOnNoValue: true
    };


    beneficiaryNameMap = {
      key: this.options.type === CommonBussinessConstant.LOCAL_BENEFICIARY_TYPE ? 'localBenefNameKey' : 'localCardBenefNameKey',
      value: '------',
      isEdit: false
    };

    // load value on card expend
    beneficiaryBankMap = {
      key: this.options.type === CommonBussinessConstant.LOCAL_BENEFICIARY_TYPE ? 'localBenefBankNameKey' : 'localCardBenefBankNameKey',
      value: '--- --- ---',
      isEdit: false,
      isDetailOption: true,
    };

    benefBankBranchMap = {
      key: this.options.type === CommonBussinessConstant.LOCAL_BENEFICIARY_TYPE ? 'benefBranchTxtKey' : 'localCardBenefBranchTxtKey',
      value: '--- --- ---',
      isEdit: false,
      isDetailOption: true,
    };

    beneficiaryBvnMap = {
      key: 'benefBvnKey',
      value: '---',
      isEdit: false,
      isDetailOption: true,
      showMappingOnNoValue: true
    };

    headerTitleMap = {
      key: this.options.type === CommonBussinessConstant.LOCAL_BENEFICIARY_TYPE ? 'localTitleKey' : 'localCardTitleKey',
      value: this.options.type === CommonBussinessConstant.LOCAL_BENEFICIARY_TYPE ? 'localTitleKey' : 'localCardTitleKey',
      isEdit: false
    };

    headerSubtitleMap = {
      key: 'subTitle',
      value: 'nickName',
      isEdit: true
    };


    // key here will be used as label in look up details component

    this.labelValuesMap.set('benef_name_key', beneficiaryNameMap);
    this.labelValuesMap.set('account_number_key', beneficiaryAccountNumberMap);
    this.labelValuesMap.set('card_number_key', beneficiaryCardNumberMap);
    // load on card expend
    this.labelValuesMap.set('bank_key', beneficiaryBankMap);
    this.labelValuesMap.set('branch_key', benefBankBranchMap);
    this.labelValuesMap.set('beneficiary_bvn_key', beneficiaryBvnMap);

    // will be used as header to template card in look up details component
    this.headerMap.set('title', headerTitleMap);
    this.headerMap.set('subTitle', headerSubtitleMap);
    this.requestMap.set('nickNameNumber', 'nickNameNumber');
    this.requestMap.set('oldNickName', 'benefName');
    this.editRequestMap.set('newNickName', 'benefName');
    this.editRequestMap.set('type', 'A');
  }

  //edited by Marina TP:#1544088 to open the payment screen with correct data depending on beneficiary type
  prepareTransaction(actionCard: IOptionsPsActionHyperlink) {
    if (this.options.type === CommonBussinessConstant.LOCAL_BENEFICIARY_TYPE) {
      actionCard.navigationOptions.queryParams.ScreenVO = {
        toAccountType: 'bankLocalTransfer',
        toBeneficiary: actionCard.navigationOptions.queryParams.accountNumber,
        toBeneficiary_lookupKey: actionCard.navigationOptions.queryParams.accountNumber,
        bank: actionCard.navigationOptions.queryParams.benefBankId,
        currency: actionCard.navigationOptions.queryParams.currency
      };
    }
    else {
      actionCard.navigationOptions.queryParams.ScreenVO = {
        toAccountType: 'localCardSettlement',
        toBeneficiary: actionCard.navigationOptions.queryParams.cardNumber,
        toBeneficiary_lookupKey: actionCard.navigationOptions.queryParams.cardNumber,
      }
    }
  }

}

